














































.btn {
	padding: 8px;
	margin: 0 8px;
	font-family: inherit;
	font-size: 14px;
	font-weight: 500;
	text-transform: uppercase;
	background: #fff;
	border: none;
	border-radius: 2px;
	transition: 0.4s cubic-bezier(0.4, 0, 0.2, 1);

	&:hover {
		cursor: pointer;
	}
}
.submit_btn {
	color: #b10438;

	&:hover {
		background: #b104381f;
	}
}
.normal_btn {
	color: black;

	&:hover {
		background: #00000021;
	}
}
.delete_btn {
	position: absolute;
	left: 0;
}
.disabled {
	color: #c4c4c4;

	&:hover {
		background: #fff;
	}
}
